<template>
  <div class="deposit">
    <el-popover
            placement="top-start"
            trigger="hover"
            v-if="!isHelpFarmers && !isExpand"
    >
      <div style="color: red">
        <h5>客户调整到惠农服务点:</h5>
        <h5>1、当月的业绩数据会同步变化。</h5>
        <h5>2、上月(含)以前的业绩数据不变。</h5>
      </div>
      <span slot="reference" style="color: red">说明</span>
    </el-popover>
    <el-button
      style="width: 200px"
      type="warning"
      size="small"
      @click="exportDataInfos"
      :loading="loading"
      >导出全部查询数据</el-button
    >
    <el-form label-width="120">
      <el-row>
        <el-col :span="12">
          <el-form-item
            label="选择条件:"
          >
            <el-radio-group
              v-model="form.query_org"
              @change="getData"
            >
              <el-radio
                v-for="item in items3"
                :key="item.key"
                :label="item.label"
              >
                {{ item.title }}
              </el-radio>
            </el-radio-group>
          </el-form-item>
        </el-col>

        <el-col :span="12">
          <el-form-item
            label="选择纬度:"
            v-if="
              $route.path ==
                '/deposit' ||
              $route.path == '/AUM'
            "
          >
            <el-radio-group
              v-model="form.query_type"
              @change="getData"
            >
              <el-radio
                v-for="item in items2"
                :key="item.key"
                :label="item.label"
              >
                {{ item.title }}
              </el-radio>
            </el-radio-group>
          </el-form-item>
        </el-col>
      </el-row>

      <el-form-item
        label="选择报表类型:"
      >
        <el-radio-group
          v-model="form.query_level"
          @change="cleanQureyData"
        >
          <el-radio
            v-for="item in ($route.path === '/expand' ? items.filter(item => item.key != '4') : items)"
            :key="item.key"
            :label="item.label"
          >
            {{ item.title }}
          </el-radio>
        </el-radio-group>
      </el-form-item>
      <el-row>
        <el-col :span="12">
          <el-form-item
            label="数据日期:"
          >
            <el-date-picker
              :clearable="false"
              size="small"
              v-if="!isHelpFarmers"
              v-model="form.query_date"
              type="date"
            >
            </el-date-picker>
            <el-date-picker
              size="small"
              v-else
              v-model="dateRange"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
            ></el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item
            label="查询机构:"
            prop="bankid"
          >
            <div style="display: flex">
              <el-input
                readonly=""
                size="small"
                v-model="bankname"
              ></el-input>
              <BranchTreePicker
                @nodeselect="
                  nodeselectForSerch
                "
              ></BranchTreePicker>
            </div>
            <!--<el-select size="small" v-model="form.query_branch" clear></el-select>-->
          </el-form-item>
        </el-col>
      </el-row>
      <el-row
        style="position: relative"
      >
        <el-col :span="9">
          <el-form-item
            label="查询客户经理:"
            prop="mgr_id"
            v-if="
              form.query_level == '3' && form.query_org == '1'
            "
          >
            <el-select
              v-model="form.mgr_id"
              filterable
              clearable
              size="small"
            >
              <el-option
                v-for="item in sysUserList"
                :key="item.account_id"
                :label="item.name"
                :value="item.account_id"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="9">
          <el-form-item
            label="查询服务点:"
            prop="site_no"
            v-if="
              form.query_level == '4'
            "
          >
            <el-select
              v-model="form.site_no"
              filterable
              clearable
              size="small"
            >
              <el-option
                v-for="item in siteList"
                :key="item.site_no"
                :label="item.site_name"
                :value="item.site_no"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="3">
          <el-form-item label-width="0">
            <el-button
              type="primary"
              size="small"
              @click="getData"
              icon="el-icon-search"
              >查询</el-button
            >
          </el-form-item>
        </el-col>
        <div class="cus-scroll">
          <div
            class="cus-scroll-child"
            :style="{
              width: cusScrollWidth,
            }"
          ></div>
        </div>
      </el-row>
    </el-form>
    <div
      class="deposit-table"
      ref="table"
    >
      <el-table
        border
        :data="showData"
        v-loading="tableLoading"
      >
        <template
          v-for="(
            item, index
          ) in showColumns"
        >
          <el-table-column
            v-if="item.children"
            :key="index"
            :label="item.label"
            align="center"
            width="320"
          >
            <el-table-column
              v-for="(
                e, i
              ) in item.children"
              :key="i"
              :label="e.label"
              :prop="e.prop"
              align="center"
            ></el-table-column>
          </el-table-column>
          <el-table-column
            v-else
            :key="index"
            :label="item.label"
            :prop="item.prop"
            align="center"
            width="80"
          ></el-table-column>
        </template>
      </el-table>
    </div>
    <el-pagination
      style="
        text-align: center;
        margin: 10px 0;
      "
      @size-change="changeSize"
      @current-change="changePage"
      :current-page="pageNum"
      :page-sizes="[10, 20, 20, 40]"
      :page-size="pageSize"
      size="small"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>
  </div>
</template>

<script>
let itemsList = [
  {
    key: "1",
    label: "1",
    title: "分行",
  },
  {
    key: "2",
    label: "2",
    title: "支行/团队",
  },
  {
    key: "3",
    label: "3",
    title: "客户经理",
  },
  {
    key: "4",
    label: "4",
    title: "服务点",
  },
];
import columns from "../util/columns";
import expandCol from "../util/expandCol";
import helpFarmerCol from "../util/heplFarmersCol";
import BranchTreePicker from "@/views/components/BranchTreePicker";
export default {
  components: {
    BranchTreePicker,
  },
  props: {
    data: {
      type: Array,
      default: function () {
        return [];
      },
    },
    total: 0,
    loading: {
      type: Boolean,
      default: false,
    },
    tableLoading: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      form: {
        query_org: "1",
        query_type: "1",
        query_level: "1",
        query_date:
          this.$route.path === "/expand"
            ? new Date()
            : new Date(
                new Date().getTime() -
                  24 * 60 * 60 * 1000
              ),
      },
      dateRange: [
        new Date(
          new Date().getTime() -
            24 * 60 * 60 * 1000
        ),
        new Date(
          new Date().getTime() -
            24 * 60 * 60 * 1000
        ),
      ],
      items2: [
        {
          key: "1",
          label: "1",
          title: "客户纬度",
        },
        {
          key: "2",
          label: "2",
          title: "账户纬度",
        },
      ],
      items3: [
        {
          key: "1",
          label: "1",
          title: "所属机构",
        },
        {
          key: "2",
          label: "2",
          title: "落地支行",
        },
      ],
      columns,
      expandCol,
      helpFarmerCol,
      showColumns: [],
      showData: [],
      bankid: "",
      bankname: "",
      pageNum: 1,
      pageSize: 10,
      siteList: [],
      sysUserList: [],
      cusScrollWidth: 0,
    };
  },
  created() {
  },
  computed: {
    isHelpFarmers() {
      return (
        this.$route.path ===
        "/helpfarmers"
      );
    },
    isExpand() {
      return (
        this.$route.path === "/expand"
      );
    },
    items() {
      return this.form.query_org == '2' ? itemsList.filter(item => parseInt(item.key) != 3) : JSON.parse(JSON.stringify(itemsList))
    }
  },
  mounted() {
    //人员列表
    this.getAccountList();
    //站点列表
    this.getSiteSelectList();
    let cusScrollEle = document.querySelector(
      ".cus-scroll"
    );
    let ele = document.querySelector(
      ".el-table__body-wrapper"
    );
    cusScrollEle.addEventListener(
      "scroll",
      () => {
        console.log(
          cusScrollEle.scrollLeft
        );
        ele.scrollLeft =
          cusScrollEle.scrollLeft;
      }
    );
  },
  methods: {
    handleOverWidth() {
      let tableHeader = document.querySelector(
        ".el-table__header"
      );
      this.cusScrollWidth =
        tableHeader.style.width;
    },
    // 机构列表
    nodeselectForSerch(node) {
      this.bankname = node.branch_name;
      this.bankid = node.branch_id;
    },

    cleanQureyData() {
      if (
        this.form.query_level != "3"
      ) {
        this.$set(
          this.form,
          "mgr_id",
          null
        );
      }
      if (
        this.form.query_level != "4"
      ) {
        this.$set(
          this.form,
          "site_no",
          null
        );
      }
    },
    formateTime(time) {
      let date = new Date(time);
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      let d = date.getDate();
      return (
        y + '' +
        (m < 10 ? "0" + m : m) + '' +
        (d < 10 ? "0" + d : d)
      );
    },
    getData() {
        //站点列表
        this.getSiteSelectList();

      this.showData = [];
      let opt = JSON.parse(
        JSON.stringify(this.form)
      );
      if (this.isHelpFarmers) {
        delete opt.query_date;
        if (this.dateRange) {
          opt.start_date = this.formateTime(
            this.dateRange[0]
          );
          opt.end_date = this.formateTime(
            this.dateRange[1]
          );
        } else {
          opt.start_date = "";
          opt.end_date = "";
        }
      }
      if (
        opt.query_date !== undefined
      ) {
        if (!this.isHelpFarmers) {
          if (this.isExpand) {
            opt.query_date = this.formateTime(
              opt.query_date ||
                new Date()
            );
          } else {
            opt.query_date = this.formateTime(
              opt.query_date ||
                new Date(
                  new Date().getTime() -
                    24 * 60 * 60 * 1000
                )
            );
          }
        }
      }
      this.$emit("get-data", {
        page: this.pageNum - 1,
        number: this.pageSize,
        bankid: this.bankid,
        ...opt,
      });
    },

    // 机构下用户列表
    getAccountList() {
      if (this.sysUserList.length != 0)
        return;
      this.$api
        .getAccountList({})
        .then((res) => {
          this.sysUserList = res.data;
        });
    },
    //站点列表
    getSiteSelectList() {
        if(this.form.query_org == '1'){
            //所属机构
            this.$api
                .getSiteSelectList()
                .then((res) => {
                    this.siteList =
                        res.data.siteList;
                });
        }else{
            //落地支行
            this.$api
                .getSiteSelectList({
                    "zh_flag" : "1"
                })
                .then((res) => {
                    this.siteList =
                        res.data.siteList;
                });
        }
    },

    // 导出
    exportDataInfos() {
      let opt = JSON.parse(
        JSON.stringify(this.form)
      );
      if (this.isHelpFarmers) {
        delete opt.query_date;
        if (this.dateRange) {
          opt.start_date = this.formateTime(
            this.dateRange[0]
          );
          opt.end_date = this.formateTime(
            this.dateRange[1]
          );
        } else {
          opt.start_date = "";
          opt.end_date = "";
        }
      }
      if (
        opt.query_date !== undefined
      ) {
        if (!this.isHelpFarmers) {
          if (this.isExpand) {
            opt.query_date = this.formateTime(
              opt.query_date ||
                new Date()
            );
          } else {
            opt.query_date = this.formateTime(
              opt.query_date ||
                new Date(
                  new Date().getTime() -
                    24 * 60 * 60 * 1000
                )
            );
          }
        }
      }

      this.$emit("export-data", {
        bankid: this.bankid,
        ...opt,
      });
    },

    // 处理列头
    handleColumns() {
      if (
        this.form.query_level == "1"
      ) {
        this.showColumns = [
          {
            label: "分行号",
            prop: "fh_branch_id",
          },
          {
            label: "分行",
            prop: "fh_branch_name",
          },
        ].concat(this.showColumns);
      } else if (
        this.form.query_level == "2"
      ) {
        this.showColumns = [
          {
            label: "支行号/团队号",
            prop: "branch_id",
          },
          {
            label: "支行/团队",
            prop: "branch_name",
          },
        ]
          .concat(this.showColumns)
          .concat([
            {
              label: "所属分行",
              prop: "fh_branch_name",
            },
          ]);
      } else if (
        this.form.query_level == "3"
      ) {
        this.showColumns = [
          {
            label: "客户经理号",
            prop: "mgr_id",
          },
          {
            label: "客户经理",
            prop: "mgr_name",
          },
        ]
          .concat(this.showColumns)
          .concat([
            {
              label: "所属分行",
              prop: "fh_branch_name",
            },
          ]);
      } else if (
        this.form.query_level == "4"
      ) {
        this.showColumns = [
          {
            label: "服务点号",
            prop: "site_no",
          },
          {
            label: "服务点",
            prop: "site_name",
          },
        ]
          .concat(this.showColumns)
          .concat([
            {
              label: "所属分行",
              prop: "fh_branch_name",
            },
            {
              label: "所属支行/团队",
              prop: "branch_name",
            },
          ]);
      }
    },
    // 修改页面大小
    changeSize(size) {
      this.pageSize = size;
      this.getData();
    },
    // 跳转页码
    changePage(page) {
      this.pageNum = page;
      this.getData();
    },
  },
  watch: {
    data: {
      handler() {
        this.showData = JSON.parse(
          JSON.stringify(this.data)
        );
        this.$nextTick(() => {
          this.handleOverWidth();
        });
      },
      deep: true,
    },
    "form.query_level": {
      handler() {
        if (
          this.$route.path === "/expand"
        ) {
          this.showColumns = JSON.parse(
            JSON.stringify(
              this.expandCol
            )
          );
        } else if (
          this.$route.path ===
          "/helpfarmers"
        ) {
          this.showColumns = JSON.parse(
            JSON.stringify(
              this.helpFarmerCol
            )
          );
        } else {
          this.showColumns = JSON.parse(
            JSON.stringify(this.columns)
          );
        }
        this.handleColumns();
        this.pageNum = 1;
        this.pageSize = 10;
        this.getData();
      },
      immediate: true,
    },
  },
};
</script>

<style lang="scss" scoped>
.deposit {
  display: flex;
  flex-flow: column;
  height: 100%;
  position: relative;
  &-table {
    flex: 1;
  }
  .cus-scroll {
    width: calc(100%);
    overflow-x: scroll;
    height: 20px;
    background: rgba(0, 0, 0, 0.4);
    position: absolute;
    top: 38px;
    &-child {
      height: 10px;
      background: white;
    }
  }
  .cus-scroll::-webkit-scrollbar {
    height: 20px;
  }
  .cus-scroll::-webkit-scrollbar-track {
    height: 20px;
  }
}
</style>



